import request, { SERVICE_MODULE_MAP } from "@/utils/request";

export class GoodsManageApi {

  /**
   * @param 
   * @param {page}  
   * @param {pageSize}  
   * @param {type}  0:;1:;2:(int)
   * @param {partType}  
   * @param {productionPlace}  
   * @param {vehBrand}   
   * @param {image}   1. 2. 0.(string)
   * @param {onShelf}    1, 2. 0.(my-skus )(string)
   * @param {keyword}  ////
   */
  getGoodsTableList(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.united,
      url: "/goods",
      method: "get",
      params
    });
  }

  // 
  getGoodsDetail(obj) {
    let params = {type:obj.type};
    return request({
      baseURL: SERVICE_MODULE_MAP.united,
      url: `/goods/${obj.id}`,
      method: "get",
      params
    });
  }

  /// 
  getGoodsCategory(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.united,
      url: `/goods/list/category`,
      method: "get",
      params
    });
  }

  //ID
  getGoodsShare(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.united,
      url: `/goods/share`,
      method: "get",
      params
    });
  }

  // 
  uploadImages(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.united,
      url: `/goods/update/upload-images`,
      method: "post",
      data: params
    });
  }

  // 
  uploadFileImg(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: `/img/upToken`,
      method: "get",
      data: params
    });
  }

  //
  getBrands(params){
    return request({
      baseURL: SERVICE_MODULE_MAP.basic,
      url: `/brands?type=1,2,3`,
      method: "get",
      params

    });
  }
  
  //
  partsClean(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.united,
      url: `/company-parts/clean?brand=${params.brand}`,
      method: "post",
      data: params.obj
    });
  }
}
